<template>
  <div class="d-flex flex-column justify-start" style="width: 100%">
    <div class="mt-3 mt-sm-4 mt-md-5 mt-lg-6 mt-xl-7"/>
    <div class="item-zoom">
      <a
        :class="`font-weight-bold page-text ${titleColor} raleway`"
        @click="() => { selected = !selected; }"
      >
        {{source.employerName}}
      </a>
    </div>
    <v-expand-transition>
      <div :class="'item-viz' + (selected ? ' item-focused' : '')" v-show="selected">
        <p class="page-sub-text px-4 pt-4 pb-0 raleway">
          {{industry}}
          <br v-if="industry">
          <br v-if="industry">
          {{description}}
          <br v-if="description">
          <br v-if="description">
          {{employees}}
          <br v-if="employees">
          <br v-if="employees">
          {{address}}
          <br v-if="address">
          <br v-if="address">
          {{phone}}
          <br v-if="phone">
          <br v-if="phone">
          <a
            v-if="website"
            style="color: #1d99be"
            :href="source.website"
          >
            {{source.website}}
          </a>
          <br v-if="website">
          <br v-if="website">
          {{naicis}}
        </p>
        <div class="d-flex px-4 pt-0 pb-4 align-center justify-center" style="width: 100%">
          <v-btn
            icon
            :disabled="updating"
            :loading="updating"
            :color="added ? '#dd7955' : 'grey'"
            class="mx-2 mt-2"
          >
            <v-icon
              v-if="added"
              @click="() => update('delete')"
            >
              mdi-account-remove-outline
            </v-icon>
            <v-icon
              v-if="!added"
              @click="() => update('add')"
            >
              mdi-account-plus-outline
            </v-icon>
          </v-btn>
          <p class="page-sub-text pa-0 ma-0 raleway">
            {{ added ? 'Remove Employer from Contacts' : 'Add Employer to Contacts' }}
          </p>
        </div>
      </div>
    </v-expand-transition>
    <div class="mb-2 mb-sm-3 mb-md-4 mb-lg-5 mb-xl-6"/>
    <v-divider v-if="source.index < (numItems - 1)"></v-divider>
  </div>
</template>

<script>
import {
  app,
  appData,
  removeUserContact,
  updateUserContact,
  subscribeToContacts,
  unsubscribeToContacts,
} from '../backend';

export default {
  name: 'EmployerItem',
  data: () => ({
    added: false,
    updating: false,
    selected: false,
  }),
  props: {
    source: Object,
    numItems: Number,
  },
  methods: {
    update(type) {
      const currentUser = app.auth().currentUser.uid;
      const companyId = this.source.id;
      if (type === 'add') updateUserContact(currentUser, companyId);
      else if (type === 'delete') removeUserContact(currentUser, companyId);
      this.updating = true;
    },

    injestContacts(contacts) {
      this.added = contacts.indexOf(this.source.id) >= 0;
      this.updating = false;
    },
  },
  computed: {
    titleColor() {
      return this.added ? 'deep-orange--text' : 'black--text';
    },

    industry() {
      if (!this.source.industry || this.source.industry === 'N/A') return null;
      return this.source.industry;
    },

    phone() {
      if (!this.source.telephone || this.source.telephone === 'N/A') return null;
      return this.source.telephone;
    },

    description() {
      if (!this.source.subIndustry || this.source.subIndustry === 'N/A') return null;
      return this.source.subIndustry;
    },

    address() {
      if (!this.source.employerAddress || this.source.employerAddress === 'N/A') return null;
      return this.source.employerAddress;
    },

    employees() {
      if (!this.source.employerSizeClass || this.source.employerSizeClass === 'N/A') return null;
      return this.source.employerSizeClass;
    },

    website() {
      return this.source.website && this.source.website !== 'N/A';
    },

    naicis() {
      if (this.source.naicis === 'N/A') return null;
      return `NAICIS: ${this.source.naicis}`;
    },
  },
  mounted() {
    this.injestContacts(appData.contacts);
    subscribeToContacts(this.injestContacts);
  },
  destroyed() {
    unsubscribeToContacts(this.injestContacts);
  },
};
</script>

<style>
.item-zoom {
  transition: transform .2s;
}

.item-zoom:hover {
  transform: scale(1.15);
}

.item-viz {
  opacity: 0;
  transition: opacity 0.15s;
}

.item-focused {
  opacity: 1;
}
</style>
