import 'firebase/firestore';
import 'firebase/storage';
import 'firebase/functions';

const config = {
  apiKey: process.env.VUE_APP_API_KEY,
  authDomain: process.env.VUE_APP_AUTH_DOMAIN,
  databaseURL: process.env.VUE_APP_DB_URL,
  projectId: process.env.VUE_APP_PROJ_ID,
  storageBucket: process.env.VUE_APP_STORAGE_BUCKET,
  messagingSenderId: process.env.VUE_APP_MS_ID,
  appId: process.env.VUE_APP_ID,
  measurementId: process.env.VUE_APP_MEASURMENT_ID,
};

console.log(process.env.VUE_APP_API_KEY);
const app = firebase.initializeApp(config);
const db = firebase.firestore(app);

const districts = [
  ['Moss Landing', 'Prunedale', 'Castroville', 'Marina', 'Oak Hills', 'Neponset', 'Blanco'],
  ['Pacific Grove', 'Del Monte Forest', 'Monterey'],
  ['Monterey', 'Seaside', 'Marina', 'Del Rey Oaks', 'Carmel-By-The-Sea', 'Hacienda Carmel', 'Rancho Tierra Grande', 'Corral De Tierra'],
  ['Boronda', 'Salinas', 'Spreckles', 'Corral De Tierra', 'Springtown', 'Bolas Knolls', 'Blanco'],
  ['Carmel-By-The-Sea', 'Carmel Highlands', 'Notleys Landing', 'Carmel Valley', 'Hacienda Carmel', 'Rancho Tierra Grande', 'Jamesburg'],
  ['Chualar', 'Penvir', 'Dean', 'Gonzales', 'Molus'],
  ['Molus', 'Camphora', 'Soledad', 'Fort Romie', 'Harlem'],
  ['Millers Ranch', 'Sycamore Flat', 'Greenfield', 'Metz'],
  ['Harlem', 'Metz', 'Elsa', 'King City'],
  ['Bitterwatter', 'Lonoak'],
  ['Lucia', 'Plaskett', 'Gorda'],
  ['Lockwood', 'Jolon'],
  ['San Lucas', 'Lonoak'],
  ['San Andro', 'Wunpost'],
  ['Bradley', 'Valleton', 'Nacimiento', 'San Miguel', 'Wunpost'],
];

let subscription = null;
const subscribers = [];
const appData = {
  contacts: [],
};

export function subscribeToContacts(func) {
  if (subscribers.indexOf(func) < 0) subscribers.push(func);
}

export function unsubscribeToContacts(func) {
  const fIndex = subscribers.indexOf(func);
  if (fIndex >= 0) subscribers.splice(fIndex, 1);
}

export function startContactSubscription(userId) {
  if (subscription) subscription();

  subscription = db.doc(`users/${userId}`).onSnapshot((doc) => {
    if (!doc.exists) return;
    appData.contacts = doc.data().contacts;
    subscribers.forEach((f) => {
      if (f) f(appData.contacts);
    });
  });
}

export function endContactSubscription() {
  if (subscription) subscription();
}

let displaySubscribed = false;
const employerSubscribers = [];

function idsToDocs(contacts) {
  Promise.allSettled(contacts.map((id) => (
    db.doc(`employer/${id}`).get().then((doc) => ({
      id: doc.id,
      ...doc.data(),
    }))
  ))).then((results) => {
    const data = [];

    results.forEach((r) => {
      if (r.status === 'fulfilled') data.push(r.value);
      else console.log(r);
    });

    employerSubscribers.forEach((f) => {
      if (f) f(data);
    });
  }).catch((e) => console.error(e));
}

export function startContactDisplay(func) {
  if (!displaySubscribed) subscribers.push(idsToDocs);
  displaySubscribed = true;
  if (employerSubscribers.indexOf(func) < 0) employerSubscribers.push(func);
  idsToDocs(appData.contacts);
}

export function endContactDisplay(func) {
  const fIndex = employerSubscribers.indexOf(func);
  if (fIndex >= 0) employerSubscribers.splice(fIndex, 1);
  if (employerSubscribers.length === 0) {
    displaySubscribed = false;
    subscribers.splice(subscribers.indexOf(idsToDocs), 1);
  }
}

export function search(ids, industries, callback, fail) {
  const employers = db.collection('employer');
  const cities = [];
  for (let i = 0; i < ids.length; i += 1) {
    const district = districts[ids[i]];
    for (let j = 0; j < district.length; j += 1) {
      if (!cities.includes(district[j])) {
        cities.push(district[j]);
      }
    }
  }

  const requests = [];
  const cityRequests = [];
  for (let i = 0; i < cities.length; i += 10) {
    cityRequests.push(employers.where('city', 'in', cities.slice(i, i + 10)));
  }

  if (cityRequests.length === 0) {
    for (let i = 0; i < industries.length; i += 10) {
      requests.push(employers.where('industry', 'in', industries.slice(i, i + 10)));
    }
  } else {
    for (let i = 0; i < cityRequests.length; i += 1) {
      for (let j = 0; j < industries.length; j += 1) {
        requests.push(cityRequests[i].where('industry', '==', industries[j]));
      }
    }
  }

  if (requests.length === 0) {
    employers.get().then((snapshot) => {
      const data = [];
      const statDict = { TOTAL: 0 };

      snapshot.forEach((doc) => {
        const employer = doc.data();

        data.push({
          id: doc.id,
          ...employer,
        });

        statDict[employer.industry] = (statDict[employer.industry] || 0) + 1;
        statDict.TOTAL += 1;
      });

      if (data.length > 0) {
        callback(
          data,
          Object.keys(statDict).map((industry) => ({
            industry,
            count: statDict[industry],
            percent: Math.round((statDict[industry] * 100) / statDict.TOTAL),
          })).sort((a, b) => b.count - a.count),
        );
      } else fail();
    }).catch(() => fail());
    return;
  }

  const sentRequests = requests.map((r) => (
    r.get().then((snapshot) => {
      const data = [];

      snapshot.forEach((doc) => {
        data.push({
          id: doc.id,
          ...doc.data(),
        });
      });

      return data;
    })
  ));

  Promise.allSettled(sentRequests).then((results) => {
    let data = [];

    results.forEach((r) => {
      if (r.status === 'fulfilled') data = data.concat(r.value);
    });

    for (let i = 0; i < data.length; i += 1) {
      for (let j = i + 1; j < data.length; j += 1) {
        if (data[i].id === data[j].id) {
          data.splice(j, 1);
          j -= 1;
        }
      }
    }

    const statDict = { TOTAL: 0 };
    data.forEach((e) => {
      statDict[e.industry] = (statDict[e.industry] || 0) + 1;
      statDict.TOTAL += 1;
    });

    if (data.length > 0) {
      callback(
        data,
        Object.keys(statDict).map((industry) => ({
          industry,
          count: statDict[industry],
          percent: Math.round((statDict[industry] * 100) / statDict.TOTAL),
        })).sort((a, b) => b.count - a.count),
      );
    } else fail();
  });
}

