export function updateUserContact(userId, companyId) {
  // updateCompanyContact(userId, companyId)
  const user = db.collection('users').doc(userId);
  user.set(
    { contacts: firebase.firestore.FieldValue.arrayUnion(companyId) },
    { merge: true },
  ).then(() => {
    console.log(`Document successfully updated with ${companyId}`);
  }).catch((error) => {
    console.error(`Error updating document: ${error} `);
  });
}

export function removeUserContact(userId, companyId) {
  // removeCompanyContact(userId, companyId)
  const user = db.collection('users').doc(userId);
  user.set(
    { contacts: firebase.firestore.FieldValue.arrayRemove(companyId) },
    { merge: true },
  ).then(() => {
    console.log(`Document successfully removed ${companyId}`);
  }).catch((error) => {
    console.error(`Error updating document: ${error} `);
  });
}

