<template>
  <v-row justify="center">
    <v-dialog v-model="open" max-width="750">
      <template v-slot:activator="{ on, attrs }">
        <v-btn
          tile
          block
          outlined
          color="#167998"
          v-bind="attrs"
          v-on="on"
          class="raleway"
        >
          Contact List
        </v-btn>
      </template>
      <v-card>
        <div class="d-flex align-end justify-end pt-1 pr-1" style="width: 100%">
          <v-btn icon small color="#ec5439" @click="() => open = false">
            <v-icon>mdi-close</v-icon>
          </v-btn>
        </div>
        <div
          class="page-text raleway text-center text-weight-bold pt-4"
          style="width: 100% !important;"
        >
          Contacts
        </div>
        <div
          class="d-flex align-start flex-column pa-3 pa-sm-4 pa-md-5 pa-lg-6 pa-xl-7"
          style="width: 100%; height: 75vh"
        >
          <search-bar
            label="Search Contacts"
            :searching="false"
            :failed="failed"
            :onUpdate="onUpdate"
            ref="search"
          ></search-bar>
          <virtual-list
            class='no-scrollbar flex-grow-1'
            style='overflow-y: auto; width: 100%'
            data-key='id'
            :data-sources='results'
            :data-component='itemComponent'
            :estimate-size="300"
            :extra-props="{ numItems: count }"
            v-if="!failed"
          />
          <div
            v-else
            class="d-flex flex-shrink-0 flex-grow-1 align-center justify-center"
            style="width: 100%"
          >
            <p class="page-text raleway text-center">No Contacts to Display!</p>
          </div>
        </div>
      </v-card>
    </v-dialog>
  </v-row>
</template>

<script>
/* eslint-disable no-restricted-globals */
/* eslint-disable no-alert */
import Fuse from 'fuse.js';
import { startContactDisplay, endContactDisplay } from '../backend';
import SearchBar from './SearchBar.vue';
import EmployerItem from './EmployerItem.vue';

export default {
  name: 'Login',
  components: { SearchBar },
  data: () => ({
    open: false,
    failed: false,
    contacts: [],
    params: {},
    count: 0,
    itemComponent: EmployerItem,
  }),
  methods: {
    onUpdate() {
      this.params = this.$refs.search.searchParams();
    },

    populateContacts(contacts) {
      this.contacts = contacts;
    },
  },
  computed: {
    results() {
      // eslint-disable-next-line vue/no-side-effects-in-computed-properties
      this.failed = false;
      let data = this.contacts;

      console.log(data);
      if (this.params.filter && this.params.filter.length > 0) {
        data = data.filter((e) => this.params.filter.indexOf(e.industry) >= 0);
        // eslint-disable-next-line vue/no-side-effects-in-computed-properties
        if (data.count === 0) this.failed = true;
      }
      if (this.params.query && this.params.query.trim()) {
        data = (
          (new Fuse(data, { keys: ['employerName', 'subIndustry', 'address', 'city', 'employerAddress', 'telephone', 'naicis'] }))
            .search(this.params.query.trim())
            .map((v) => v.item)
        );
        // eslint-disable-next-line vue/no-side-effects-in-computed-properties
        if (data.count === 0) this.failed = true;
      }
      data = data.map((e, index) => ({ index, ...e }));
      console.log(data);
      // eslint-disable-next-line vue/no-side-effects-in-computed-properties
      this.count = data.count;
      return data;
    },
  },
  watch: {
    open: {
      immediate: true,
      handler(newO, oldO) {
        if (newO === oldO) return;
        if (newO) startContactDisplay(this.populateContacts);
        else endContactDisplay(this.populateContacts);
      },
    },
  },
};
</script>
