<template>
  <v-autocomplete
    v-model="selectedIndustries"
    :items="industries"
    chips
    label="Select Industries..."
    multiple
    solo
    class="raleway"
    color="cyan darken-3"
    item-color="cyan darken-3"
  >
    <template v-slot:selection="data">
      <v-chip
        label
        text-color="white"
        color="cyan darken-3"
        v-bind="data.attrs"
        class="raleway"
        :input-value="data.selected"
        close
        @click="data.select"
        @click:close="remove(data.item)"
      >
        <v-tooltip bottom>
          <template v-slot:activator="{ on, attrs }">
            <span
              v-bind="attrs"
              v-on="on"
              class="d-inline-block text-truncate"
              style="max-width: 15ch;"
            >
              {{ data.item }}
            </span>
          </template>
          <span class="raleway">{{ data.item }}</span>
        </v-tooltip>
      </v-chip>
    </template>
    <template v-slot:item="data">
      <template>
        <v-list-item-content>
          <v-tooltip bottom>
            <template v-slot:activator="{ on, attrs }">
              <span
                class="raleway d-inline-block text-truncate text-left"
                style="max-width: 40ch; height: 2ch;"
                v-bind="attrs"
                v-on="on"
              >
                {{ data.item }}
              </span>
            </template>
            <span class="raleway">{{ data.item }}</span>
          </v-tooltip>
        </v-list-item-content>
      </template>
    </template>
  </v-autocomplete>
</template>

<script>
export default {
  name: 'IndustryFilter',
  data: () => ({
    selectedIndustries: [],
    industries: [
      'Agriculture, Forestry, Fishing and Hunting',
      'Mining',
      'Utilities',
      'Construction',
      'Manufacturing',
      'Wholesale Trade',
      'Retail Trade',
      'Transportation and Warehousing',
      'Information',
      'Finance and Insurance',
      'Real Estate Rental and Leasing',
      'Professional, Scientific, and Technical Services',
      'Management of Companies and Enterprises',
      'Administrative and Support and Waste Management and Remediation Services',
      'Educational Services',
      'Health Cate and Social Assistance',
      'Arts, Entertainment, and Recreation',
      'Accommodation and Food Services',
      'Other Services (except Public Administration)',
      'Public Administration',
      'Nonclassified',
    ],
  }),
  props: {
    onUpdate: Function,
  },
  methods: {
    remove(item) {
      const index = this.selectedIndustries.indexOf(item);
      if (index >= 0) this.selectedIndustries.splice(index, 1);
    },

    getIndustries() {
      return this.selectedIndustries;
    },
  },
  watch: {
    selectedIndustries(val) {
      if (val && this.onUpdate) this.onUpdate();
    },
  },
};
</script>
