<template>
  <div style="width: 100%">
    <div style="width: 100%" class="d-flex">
      <v-btn icon color="grey" class="mr-2 mt-2">
        <v-icon
          v-if="showIndustries"
          @click="() => { showIndustries = false; }"
        >
          mdi-chevron-up
        </v-icon>
        <v-icon
          v-if="!showIndustries"
          @click="() => { showIndustries = true; }"
        >
          mdi-chevron-down
        </v-icon>
      </v-btn>
      <v-text-field
        v-model="query"
        :solo="true"
        :clearable="true"
        :label="`${label}...`"
        type="text"
        :loading="searching ? 'cyan darken-3' : false"
        :error-messages="failed ? 'Unable to find employers!' : undefined"
        class="flex-grow-1 raleway"
        @keydown.enter="search"
      >
      </v-text-field>
      <v-tooltip top v-if="searchButton">
        <template v-slot:activator="{ on, attrs }">
          <v-btn
            v-bind="attrs"
            v-on="on"
            icon
            @click="search"
            color="cyan darken-3"
            class="ml-2 mt-2"
          >
            <v-icon>mdi-account-search-outline</v-icon>
          </v-btn>
        </template>
        <span class="raleway">{{ label }}</span>
      </v-tooltip>
    </div>
    <v-fade-transition>
      <industry-filter v-show="showIndustries" ref="filter" :onUpdate="onUpdate"></industry-filter>
    </v-fade-transition>
  </div>
</template>

<script>
import IndustryFilter from './IndustryFilter.vue';

export default {
  data: () => ({
    showIndustries: false,
    query: '',
  }),
  components: { IndustryFilter },
  props: {
    search: Function,
    searching: Boolean,
    failed: Boolean,
    label: String,
    searchButton: Boolean,
    onUpdate: Function,
  },
  methods: {
    searchParams() {
      return ({
        query: this.query,
        filter: this.$refs.filter.getIndustries(),
      });
    },
  },
  watch: {
    query(val, oldVal) {
      if (val !== oldVal && this.onUpdate) this.onUpdate();
    },
  },
};
</script>
