<template>
  <v-row justify="center">
    <v-dialog v-model="open" :persistent="!optional" max-width="500">
      <template v-slot:activator="{ on, attrs }">
        <v-btn
          absolute
          dark
          fab
          top
          left
          color="#dd7955"
          v-bind="attrs"
          v-on="on"
        >
          <v-icon>mdi-card-account-details-outline</v-icon>
        </v-btn>
      </template>
      <v-card>
        <div v-if="optional" class="d-flex align-end justify-end pt-1 pr-1" style="width: 100%">
          <v-btn icon small color="#ec5439" @click="() => open = false">
            <v-icon>mdi-close</v-icon>
          </v-btn>
        </div>
        <div
          class="page-text raleway text-center text-weight-bold pt-4"
          style="width: 100% !important;"
        >
          Login Portal
        </div>
        <v-form
          ref="form"
          v-model="valid"
          class="pa-4"
        >
          <v-text-field
            v-model="email"
            class="raleway"
            solo
            :rules="emailRules"
            type="text"
            label="E-mail"
          ></v-text-field>
          <v-text-field
            v-model="password"
            class="raleway"
            solo
            :append-icon="showPass ? 'mdi-eye' : 'mdi-eye-off'"
            :rules="passwordRules"
            :type="showPass ? 'text' : 'password'"
            label="Password"
            counter
            @click:append="showPass = !showPass"
          ></v-text-field>
          <div class="d-flex align-start justify-start" style="width: 100%">
            <a
              text
              class="page-sub-text raleway"
              style="color: #dd7955;"
              @click="resetPassword"
            >
              Reset Password
            </a>
          </div>
        </v-form>
        <v-card-actions>
          <v-btn
            v-if="signedIn"
            :loading="signingOut"
            :disabled="signingIn || signingUp"
            color="purple darken-3"
            class="raleway"
            text
            @click="signOut"
          >
            Sign Out
          </v-btn>
          <v-spacer></v-spacer>
          <v-btn
            :loading="signingUp"
            :disabled="signingOut || signingIn || !valid"
            color="#dd7955"
            class="raleway"
            text
            @click="signUp"
          >
            Sign Up
          </v-btn>
          <v-spacer v-if="!signedIn"></v-spacer>
          <v-btn
            :loading="signingIn"
            :disabled="signingOut || signingUp || !valid"
            color="cyan darken-3"
            class="raleway"
            text
            @click="signIn"
          >
            Sign In
          </v-btn>
          <v-spacer v-if="!signedIn"></v-spacer>
        </v-card-actions>
      </v-card>
    </v-dialog>
    <v-snackbar
      v-model="errorNotification"
      dark
      color="#ec5439"
      :timeout="4000"
    >
      {{ errorMessage }}
    </v-snackbar>
  </v-row>
</template>

<script>
/* eslint-disable no-restricted-globals */
/* eslint-disable no-alert */
import { app, startContactSubscription, endContactSubscription } from '../backend';

export default {
  name: 'Login',

  data: () => ({
    optional: false,
    open: false,
    signingUp: false,
    signingIn: false,
    signingOut: false,
    errorNotification: false,
    errorMessage: '',
    valid: false,
    email: '',
    password: '',
    signedIn: false,
    showPass: false,
    passwordRules: [
      (v) => !!v || 'Required',
      (v) => (v && v.length >= 8) || 'Must be at least 8 characters',
      (v) => (v && /[a-zA-Z]/.test(v)) || 'Must contain a letter',
      (v) => (v && /[^a-zA-Z0-9]/.test(v)) || 'Must contain a special character',
      (v) => (v && /[0-9]/.test(v)) || 'Must contain a number',
      (v) => (v && !/\s/.test(v)) || 'No spaces, tabs, etc... allowed.',
    ],
    emailRules: [
      (v) => !!v || 'Required',
      (v) => /.+@.+/.test(v) || 'E-mail must be valid',
    ],
  }),
  methods: {
    signIn() {
      if (this.signingUp || this.signingOut) return;
      this.signingIn = true;

      app.auth().signInWithEmailAndPassword(this.email, this.password)
        .then(() => {
          this.signingIn = false;
          this.open = false;
          this.password = '';
        })
        .catch((error) => {
          this.signingIn = false;
          this.errorMessage = `Error signing up: ${error.message}`;
          this.errorNotification = true;
        });
    },

    signUp() {
      if (this.signingIn || this.signingOut) return;
      this.signingUp = true;

      app.auth().createUserWithEmailAndPassword(this.email, this.password)
        .then((user) => {
          user.user.sendEmailVerification();
          this.signingUp = false;
          this.open = false;
          this.password = '';
        })
        .catch((error) => {
          this.signingUp = false;
          this.errorMessage = `Error signing up: ${error.message}`;
          this.errorNotification = true;
        });
    },

    signOut() {
      if (this.signingUp || this.signingIn) return;
      this.signingOut = true;
      endContactSubscription();

      app.auth().signOut()
        .then(() => {
          this.signingOut = false;
          this.open = false;
        })
        .catch((error) => {
          this.signingOut = false;
          this.errorMessage = `Error signing out: ${error.message}`;
          this.errorNotification = true;
        });
    },

    resetPassword() {
      const user = app.auth().currentUser;
      const email = user ? user.email : this.email;
      if (email && /.+@.+/.test(email)) {
        if (confirm(`Reset the password for ${email}?`)) {
          app.auth().sendPasswordResetEmail(email)
            .then(() => {
              this.errorMessage = 'An email has been sent to reset your password';
              this.errorNotification = true;
            })
            .catch((error) => {
              this.errorMessage = `Error starting password reset: ${error.message}`;
              this.errorNotification = true;
            });
        }
      } else {
        this.errorMessage = 'Invalid Email Address';
        this.errorNotification = true;
      }
    },
  },
  created() {
    app.auth().onAuthStateChanged((user) => {
      if (user) {
        this.optional = true;
        this.signedIn = true;
        startContactSubscription(user.uid);
      } else {
        this.signedIn = false;
        this.optional = false;
        this.open = true;
      }
    });
  },
};
</script>
