<template>
  <div id="app">
    <v-app id="earn-learn-baccc">
      <v-main>
        <login></login>
        <div class="fill-height d-flex flex-column justify-center">
          <div style="width: 100%; height: 100%" class="d-flex flex-column">
            <div
              style="width: 100%"
              :class="`d-flex flex-grow-0 align-center justify-center ${titleSpacing}`"
            >
              <img style="width: 3.5vmax; height: 3.5vmax;" src="./assets/logo.png">
              <p
                class="page-title pa-0 my-0 mx-4 font-weight-bold raleway"
                style="color: #167998"
              >
                EARN & LEARN
              </p>
            </div>
            <div class="flex-grow-0 d-flex align-center justify-center" style="width: 100%">
              <div
                :style="contactBtnSize"
                class="px-1 px-sm-2 px-md-3 px-lg-4 px-xl-5"
              >
                <contact-list></contact-list>
              </div>
            </div>
            <v-row
              class="flex-grow-1"
              align="start"
              justify="center"
              :class="searchAreaTop"
              style="align-content: flex-start !important;"
            >
              <div :style="mapSize">
                <district-map :onFocus="onFocus" :onSelect="() => {}" ref="dMap"></district-map>
              </div>
              <div
                :class="`d-flex flex-column ${searchSpacing} ${searchTop}`"
                :style="searchSize"
              >
                <search-bar
                  label="Search for Employers"
                  :search="search"
                  :searching="searching"
                  :failed="failed"
                  ref="search"
                  :searchButton="true"
                ></search-bar>
                <v-slide-y-transition>
                  <stats-dropdown
                    style="width: 100%"
                    v-if="results.length > 5"
                    :stats="stats"
                  ></stats-dropdown>
                </v-slide-y-transition>
                <v-fade-transition>
                  <virtual-list
                    class="no-scrollbar flex-grow-1"
                    style='overflow-y: auto; width: 100%'
                    data-key='id'
                    :data-sources='results'
                    :data-component='itemComponent'
                    :estimate-size="300"
                    :extra-props="{ numItems: results.length }"
                    v-if="searched"
                  />
                </v-fade-transition>
                <div
                  class='flex-grow-1 flex-shrink-0'
                  style='overflow-y: auto; width: 100%'
                  v-if="!searched"
                />
              </div>
            </v-row>
          </div>
        </div>
      </v-main>
    </v-app>
    <div class="district-tooltip" v-show="tooltipActive" :style="tooltipPos">
      {{tooltip}}
    </div>
  </div>
</template>

<script>
import VirtualList from 'vue-virtual-scroll-list';
import Fuse from 'fuse.js';
import { search } from './backend';
import DistrictMap from './components/Map.vue';
import EmployerItem from './components/EmployerItem.vue';
import Login from './components/Login.vue';
import ContactList from './components/ContactList.vue';
import SearchBar from './components/SearchBar.vue';
import StatsDropdown from './components/StatsDropdown.vue';

export default {
  name: 'App',
  data: () => ({
    tooltip: '',
    tooltipPos: '',
    tooltipActive: false,
    districts: [
      'NORTH MONTEREY COUNTY',
      'PACIFIC GROVE UNIFIED',
      'MONTEREY PENINSULA UNIFIED',
      'SALINAS UNION HIGH',
      'CARMEL UNIFIED',
      'GONZALES UNIFIED',
      'SOLEDAD UNIFIED',
      'GREENFIELD UNION',
      'KING CITY UNION',
      'BITTERWATER-TULLY',
      'PACIFIC UNIFIED',
      'SAN ANTONIO UNION',
      'SAN LUCAS UNION',
      'SAN ARDO UNION',
      'BRADLEY UNION',
    ],
    results: [],
    stats: [],
    searching: false,
    searched: false,
    failed: false,
    showIndustries: false,
    itemComponent: EmployerItem,
    searchSpacing: 'ml-3 ml-sm-4 ml-md-5 ml-lg-6 ml-xl-7',
    searchAreaTop: 'mt-3 mt-sm-3 mt-md-4 mt-lg-4 mt-xl-5',
    titleSpacing: 'ma-3 ma-sm-4 ma-md-5 ma-lg-6 ma-xl-7',
  }),
  components: {
    DistrictMap,
    VirtualList,
    Login,
    ContactList,
    SearchBar,
    StatsDropdown,
  },
  computed: {
    mapSize() {
      switch (this.$vuetify.breakpoint.name) {
        case 'xs': return 'width: 70vmin; height: 70vmin;';
        case 'sm': return 'width: 65vmin; height: 65vmin;';
        case 'md': return 'width: 45vmin; height: 45vmin;';
        case 'lg': return 'width: 55vmin; height: 55vmin;';
        case 'xl': return 'width: 50vmin; height: 50vmin;';
        default: return 'width: 85vmin; height: 85vmin;';
      }
    },
    searchSize() {
      switch (this.$vuetify.breakpoint.name) {
        case 'xs': return 'width: 70vmin; height: 45vh;';
        case 'sm': return 'width: 65vmin; height: 40vh;';
        case 'md': return 'width: 50vmin; height: 45vmin;';
        case 'lg': return 'width: 45vmin; height: 55vmin;';
        case 'xl': return 'width: 45vmin; height: 50vmin;';
        default: return 'width: 45vmin; height: 85vmin;';
      }
    },
    contactBtnSize() {
      switch (this.$vuetify.breakpoint.name) {
        case 'xs': return 'width: 70vmin;';
        case 'sm': return 'width: 65vmin;';
        case 'md': return 'width: 95vmin;';
        case 'lg': return 'width: 100vmin;';
        case 'xl': return 'width: 95vmin;';
        default: return 'width: 90vmin;';
      }
    },
    searchTop() {
      return this.$vuetify.breakpoint.mdAndDown ? this.searchAreaTop : '';
    },
  },
  methods: {
    onFocus(id, x, y) {
      if (id === -1) {
        this.tooltipActive = false;
        return;
      }

      this.tooltip = this.districts[id];
      this.tooltipActive = true;
      this.tooltipPos = `top: ${y}px; left: ${x}px;`;
    },

    search() {
      this.results = [];
      this.searching = true;
      this.searched = false;
      const params = this.$refs.search.searchParams();

      search(
        this.$refs.dMap.getSelected(),
        params.filter,
        (employers, stats) => {
          console.log(stats);
          this.failed = false;
          this.searched = true;
          this.searching = false;
          let results = [];
          if (params.query && params.query.trim()) {
            results = (
              (new Fuse(employers, { keys: ['employerName', 'subIndustry', 'address', 'city', 'employerAddress', 'telephone', 'naicis'] }))
                .search(params.query.trim())
                .map((v, index) => ({ index, ...v.item }))
            );
          } else results = employers.map((v, index) => ({ index, ...v }));
          this.results = results;
          this.stats = stats;
        },
        () => {
          this.searching = false;
          this.failed = true;
        },
      );
    },
  },
};
</script>

<style>
html { overflow-y: hidden !important; }

#app {
  font-family: Raleway, Helvetica, Arial, sans-serif !important;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  text-align: center;
  color: #2c3e50;
  margin-top: 60px;
}

.raleway {
  font-family: Raleway, Helvetica, Arial, sans-serif !important;
}

.page-title {
  font-size: calc(14px + 3vmax);
}

.page-text {
  font-size: calc(12px + 1vmin);
}

.page-sub-text {
  font-size: calc(10px + 0.75vmin);
}

.district-tooltip {
  position: absolute;
  background-color: #115d74aa;
  color: white;
  font-size: 1em;
  padding: 0.25em;
}

/* Hide scrollbar for Chrome, Safari and Opera */
.no-scrollbar::-webkit-scrollbar {
  display: none;
}

/* Hide scrollbar for IE, Edge and Firefox */
.no-scrollbar {
  -ms-overflow-style: none;  /* IE and Edge */
  scrollbar-width: none;  /* Firefox */
}
</style>
